.palette-item {
  width: 60px;
  height: 60px;
  margin: 20px;
  border-radius: 10px;
  cursor: pointer;
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.74);
}

.palette-item-active {
  width: 80px;
  height: 80px;
  margin: 10px;
  box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.74);
}

@media (max-width: 567px) {
  .palette-item {
    width: 30px;
    height: 30px;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.74);
    margin: 10px;
  }

  .palette-item-active {
    width: 40px;
    height: 40px;
    margin: 5px;
    box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.74);
  }
}
